<template>
  <div class="container">
    <div class="group1">
      <transition
        name="custom-classes-transition"
        appear-class="animated tada"
      >
        <p class="header">
          #_장호항_ㄱㄱ
        </p>
      </transition>
      <parallax>
        <img :src="images[3].source">
      </parallax>
    </div>

    <div class="content-container group2" v-scroll-reveal.reset>
      <div class="content">
        <li class="review" v-for="review of reviews" :key=review.id>
            <Review
              :title="review.title"
              :article="review.article"
            />
        </li>
      </div>
    </div>

    <div class="tag group3">
      <p class="header">
        #해수욕장_ㄱㄱ
      </p>
      <parallax
        :fixed="true"
      >
        <img :src="images[0].source">
      </parallax>
    </div>

    <div class="attraction group4" v-scroll-reveal.reset>
      <div class="region jangho-beach" onclick="location.href='https://blog.naver.com/tourjeonju?Redirect=Log&logNo=221314197673'">
        <p>#장호항</p>
      </div>

      <div class="region chuam-beach" onclick="location.href='https://blog.naver.com/bear00381?Redirect=Log&logNo=221313348996'">
        <p>#추암_해수욕장</p>
      </div>

      <div class="region mangsang-beach" onclick="location.href='http://tourhistory.tistory.com/201'">
        <p>#망상_해수욕장</p>
      </div>

      <div class="region sinnam-beach" onclick="location.href='https://m.blog.naver.com/PostView.nhn?blogId=syszero&logNo=110173654588&proxyReferer=https%3A%2F%2Fwww.google.co.kr%2F'">
        <p>#신남_해수욕장</p>
      </div>

      <div class="region mureong-valley" onclick="location.href='https://blog.naver.com/daichung/221084506445'">
        <p>#무릉_계곡</p>
      </div>
    </div>
    <div class="attraction-wrapper group5" v-scroll-reveal.reset>
      <p class="left-time">#_{{days}}일_{{hours}}시간_{{minutes}}분_{{seconds}}초_남음.</p>
      <parallax>
        <img :src="images[0].source">
      </parallax>
    </div>
  </div>
</template>

<script>
  import Review from '../screens/Review'
  import Attraction from '../screens/Attraction'
  import Parallax from 'vue-parallaxy'
  import AnimatedVue from 'animated-vue'

  export default {
    name: "Main",
    components: {
      Review,
      Attraction,
      Parallax,
      AnimatedVue
    },

    mounted() {
      this.leftDays = setInterval(this.updateCountDown, 1000);
    },

    data() {
      return {
        images: [
          {order: 1, source: 'http://drive.google.com/uc?export=view&id=1zVujXZjzOwNTFsJwSRKIphitu4gu-_zc'},
          {order: 2, source: 'http://drive.google.com/uc?export=view&id=1Jxrk1_ofoEraoADVLVmsd7TNzq364jxI'},
          {order: 3, source: 'http://drive.google.com/uc?export=view&id=1MX9Fa-cEMRq0UIrLBcqaVNs2TAUFpzMu'},
          {order: 4, source: 'http://drive.google.com/uc?export=view&id=1Fr8vapMFttUoEk4omui_cPYsZ31D78jX'},
        ],

        reviews: this.$store.state.review,

        options: {
          slidesPerView: 1,
          // spaceBetween: 50,
          // centeredSlides: true,
          // freeMode: true,
        },

        targetDate: new Date("Aug 18, 2018 00:00:00").getTime(),

        days: 0,
        hours: 0,
        minutes: 0,
        seconds: 0,
        leftDays: 0,
      }
    },

    methods: {
      updateCountDown() {
        const now = new Date().getTime();
        const distance = this.targetDate - now;

        this.days = Math.floor(distance / (1000 * 60 * 60 * 24));
        this.hours = Math.floor((distance % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
        this.minutes = Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60));
        this.seconds = Math.floor((distance % (1000 * 60)) / 1000);
      }
    },
  }
</script>

<style scoped>
  .container {
    height: 100vh;
  }

  .group1 {
    position: relative;
    display: flex;
    min-height: 300px;
    flex-direction: column;
    justify-content: center;
    z-index: 9;
  }

  .group2 {
    padding-bottom: 3rem;
  }

  .group3 {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    overflow-x: hidden;
  }

  .group4 {
    display: flex;
    height: 100vh;
    background: #ffffff;
    padding: 3rem 0 0 0;
    align-items: center;
  }

  .group5 {
    position: relative;
    width: 100%;
    height: 100vh;
    display: flex;
    z-index: 10;
    justify-content: center;
  }

  .header {
    position: absolute;
    z-index: 2;
    font-size: 5rem;
    color: #ffffff;
    /*font-weight: 700;*/
    align-self: center;

  }

  .content {
    overflow: hidden;
  }

  .content-container {
    padding-top: 3rem;
    display: block;
    position: relative;
    background-color: white;
    z-index: 1;
  }

  .review:nth-child(even) {
    position: relative;
    margin-bottom: -50px;
    right: -20%;
  }

  .review:nth-child(odd) {
    position: relative;
    margin-bottom: -50px;
    left: -20%;
  }

  .review {
    display: flex;
    justify-content: space-around;
  }

  .attraction {
    position: relative;
    z-index: 3;
  }

  .region {
    margin: 0 auto;
    width: 1000px;
    height: 500px;
    display: flex;
    align-items: center;
    text-align: center;
  }

  .region p {
    width: 100%;
    font-size: 4rem;
    font-weight: 700;
    color: white;
  }

  .jangho-beach {
    background: url("http://drive.google.com/uc?export=view&id=1Jxrk1_ofoEraoADVLVmsd7TNzq364jxI");
    background-size: cover;
  }

  .chuam-beach {
    background: url("http://drive.google.com/uc?export=view&id=1WLtFVSBKTBakm0L33c3pcCN-rebluVIV");
    background-size: cover;
  }

  .mangsang-beach {
    background: url("http://drive.google.com/uc?export=view&id=12OJXyROKSn64j8avL5uVxCjLhq-cGpMn");
    background-size: cover;
  }

  .sinnam-beach {
    background: url("http://drive.google.com/uc?export=view&id=1HcPcLT4wDc2BUyPCPBOGCrbcpMUz3nu-");
    background-size: cover;
  }

  .mureong-valley {
    background: url("http://drive.google.com/uc?export=view&id=1GGTK-aYHRlfpm-hP2clCJZQHafcFD42M");
    background-size: cover;
  }

  .left-time {
    position: absolute;
    font-size: 5rem;
    color: #ffffff;
    align-self: center;
  }
</style>



